.col-4{
    color: black;
    background-color: aqua;
    font-weight: bold;
    text-transform: capitalize;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 5px;
}
.name{
    font-size: 50px;
    color: orange;
    text-transform: uppercase;
    padding: 20px;
    font-weight: 600;
}
.title{
    font-style: italic;
    font-weight: 400;
    color: darkgray;
}
p {  
 font-family: 'Lato', sans-serif;  
 letter-spacing: 1px;  
 font-size:14px;  
 color: #333333;  
}  
.header {  
 position:relative;  
 text-align:center;  
 background: linear-gradient(60deg, rgb(20, 20, 20) 0%, rgb(2, 0, 34) 100%);  
 color:white;  
}  
.logo {  
 width:50px;  
 fill:white;  
 padding-right:15px;  
 display:inline-block;  
 vertical-align: middle;  
}  
.inner-header {  
 height:65vh;  
 width:100%;  
 margin: 0;  
 padding: 0;  
}  
.flex { /*Flexbox for containers*/  
 display: flex;  
 justify-content: center;  
 align-items: center;  
 text-align: center;  
}  
.waves {  
 position:relative;  
 width: 100%;  
 height:15vh;  
 margin-bottom:-7px; /*Fix for safari gap*/  
 min-height:90px;  
 max-height:150px;  
}  
.content {  
 position:relative;  
 height:20vh;  
 text-align:center;  
 background-color: white;  
}  
/* Animation */  
.parallax > use {  
 animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)   infinite;  
}  
.parallax > use:nth-child(1) {  
 animation-delay: -2s;  
 animation-duration: 7s;  
}  
.parallax > use:nth-child(2) {  
 animation-delay: -3s;  
 animation-duration: 10s;  
}  
.parallax > use:nth-child(3) {  
 animation-delay: -4s;  
 animation-duration: 13s;  
}  
.parallax > use:nth-child(4) {  
 animation-delay: -5s;  
 animation-duration: 20s;  
}  
@keyframes move-forever {  
 0% {  
  transform: translate3d(-90px,0,0);  
 }  
 100% {   
  transform: translate3d(85px,0,0);  
 }  
}  
/*Shrinking for mobile*/  
@media (max-width: 768px) {  
 .waves {  
  height:40px;  
  min-height:40px;  
 }  
 .content {  
  height:30vh;  
 }  
 h1 {  
  font-size:24px;  
 }  
} 